import React, { Component } from 'react';
import './ListPane.css';
import ClassList from '../ClassList';
import TopicList from './TopicList';

export default class ListPane extends Component {

    constructor(props) {
        super()
        this.state = {
            view: props.view,
        }
    }
    render() {
        if(this.props.view === "dashboard"){
            return (
                <div class="listPane">
                <ClassList classes={this.props.classes} onClickOnClass={this.props.onClickOnClass}/>
                </div>
            )
        } else if(this.props.view === "student" || this.props.view === "teacher"){
            return (
                <div class="listPane">
                    <TopicList view={this.props.view} currentClassObject={this.props.currentClassObject} topics={this.props.topics}/>
                </div>
            )
        }
        else {
            return (
                <div class="listPane">
                    not a valid view
                </div>
            )
        }
    }
}
