import React from 'react';
import './TopicList.css';
import Modals from '../../Modals/Modals';
import {componentDidMount} from 'react';

class TopicList extends React.Component {

  constructor(props) {
    super()
    this.state = {
      view: props.view,
      modalVisible: false,
      targetModal: 'Default',
      topics: [],
      getTopics: true
    }
    this.toggleModals = this.toggleModals.bind(this);
    this.createTeacherList = this.createTeacherList.bind(this);
    this.createStudentList = this.createStudentList.bind(this);
  }


  toggleModals(e) {
    e.preventDefault();
    const { modalVisible, targetModal } = this.state;
    this.setState({
      modalVisible: !modalVisible,
      targetModal: targetModal === 'Default' ? e.target.value : 'Default',
    })
  }

  createStudentList = (topics) =>{
    let list2 = [];
    console.log(" here topics are ", topics);
    list2 = topics.map((theTopic) =>
      <li className="topic" key={theTopic.title}>
      <h3>{theTopic.title}</h3>
      <p>Total Votes <span className="vote-count">{theTopic.votes}</span></p>
      <div className="check">
      <button className="badge badge-pill add-vote" name="addVote" >Add Vote</button>
      </div>
      <div className="view-create-buttons">
        <button className="btn btn-main">View Questions</button>
        <button data-topic="0" className="btn btn-main create-question" value="CreateAQuestionModal" onClick={this.toggleModals}>Create Question</button>
      </div>
      <ul className="question-list">
        {theTopic.questions.map((question) =>
        <li>
          <p>{question}</p>
        </li>
        )}
      </ul>
    </li>
    )
    return list2
  }


  createTeacherList = (topics) =>{

    let list2 = [];
    
    list2 = topics.map((theTopic) =>
    
    <li className="topic" key={theTopic.title}>
    <h3>{theTopic.title}</h3>
    <p>Total Votes <span className="vote-count">{theTopic.votes}</span></p>
    <div className="check">
      <input type="checkbox" className="delete-topic" name="deleteTopic" value="Delete Topic"/><span>Delete</span>
    </div>
    <div className="view-create-buttons">
      <button className="btn btn-main">View Questions</button>
    </div>
    <ul className="question-list">
      {theTopic.questions.map((question) =>
      <li>
        <p>{question}</p>
      </li>
      )}
    </ul>
  </li>


    )
    return list2
  }


  render() {
    if(this.props.view === "student") {
      return (
        <div>

          <ul id="topicView">
            {this.createStudentList(this.props.topics)}
          </ul>
        <Modals toggle={this.state.modalVisible} value={this.state.targetModal} onModalClose={this.toggleModals} />
      </div>
      )
    } else if(this.props.view === "teacher"){
      return (
        <div>
          <ul id="topicView">
            {this.createTeacherList(this.props.topics)}
          </ul>
        </div>
      )
    } else {
      return (
        <div>
          not a valid view
        </div>
      )
    }
  }
} 

export default TopicList;