import React, { Component } from 'react';
import './Modals.css';
import ModalsIndex from './index';

const hidden = { display: "none"};
const visible = { display: "flex" };

class Modals extends Component {
  constructor(props) {
    super(props);
    this.handleModalClose = this.handleModalClose.bind(this);
    this.state = {
      user: props.user
    }
  }

  handleModalClose(e) {
    const { onModalClose } = this.props;
    onModalClose(e);
  }

  render() {
    const {toggle, value, user} = this.props;
    const Modal = ModalsIndex[value];
    return (
      <div className="Modals" user={user} style={toggle ? visible : hidden}>
        <Modal onModalClose={this.handleModalClose} user={this.state.user}/>
      </div>
    );
  }
}

export default Modals;
