import React, { Component } from 'react';
import './CreateAClassModal.css';
import {users, classes} from '../../../firebase';

class CreateAClassModal extends Component {
  constructor(props) {
    super(props);
    this.handleModalClose = this.handleModalClose.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
    this.state = {
      user: props.user
    }
  }

  handleModalClose(e) {
    const { onModalClose } = this.props;
    onModalClose(e);
  }

  handleSubmit(e) {
    e.preventDefault();
    let myObj = {};
    let days = {};
    let duration = {};
    let time = {};
    for(var i = 0; i < e.target.length - 1; i++){
      if(e.target[i].name == "day"){
        days[e.target[i].value] = e.target[i].checked
      } else if (e.target[i].name == "private"){
        myObj[e.target[i].name] = e.target[i].checked
      } else if(e.target[i].name == "date-from") {
        if(e.target[i].value){
          duration['from'] = e.target[i].value;
        }
      } else if(e.target[i].name == "date-to") {
        if(e.target[i].value){
          duration['to'] = e.target[i].value;
        }
      } else if(e.target[i].name == "time-from") {
        if(e.target[i].value){
          time['from'] = e.target[i].value;
        }
      } else if(e.target[i].name == "time-to") {
        if(e.target[i].value){
          time['to'] = e.target[i].value;
        }
      } else {
        if(e.target[i].value){
          myObj[e.target[i].name] = e.target[i].value;
        }
      }
      myObj['duration'] = duration;
      myObj['time'] = time;
      myObj['topics'] = [];

    }
    myObj['days'] = days;
    myObj['teacher'] = this.state.user;
    const _this = this;
 
    classes.add(myObj).then(function(newClass){
      let userRef = _this.state.user;
      userRef.get().then(function(user){
        let classes = user.data().classes.teacher;
        let newClassObject = {};
        newClassObject['class'] = newClass;
        newClassObject['title'] = myObj['title'];
        classes.push(newClassObject);
        console.log("classes is ", classes);
        userRef.update({
          "classes.teacher": classes
        }).then(function(res){
          console.log("successfully updated!");
        }).catch(function(err){
          console.log("err", err);
        })
      }).catch(function(err){
        console.log("err", err);
      })

      
    }).catch(function(err){
      console.log("err", err);
    })

    this.handleModalClose(e);
  }
    render(){
        return(
            <main id="createAClassModal" className="our-modal">
            <span className="close" onClick={this.handleModalClose}>&times;</span>
      
            <header>
              <h1>Create a Class</h1>
            </header>
      
            <section id="createAClassForm">
              <form id="createAClass" onSubmit={this.handleSubmit}>
              
                Course Title* <input className="text-box" type="text" name="title" required/><br />
                Description of Class* <input className="text-box" type="text" name="description" required/><br />
      
                <p>Date From</p>
                <div className="date-from">
                  <input type="date" name="date-from"/>
                  <p>to</p>
                  <input type="date" name="date-to"/>
                </div>

                {/* <p id="days">Days</p> */}
                <div className="days-container" id="days">
                  <div><input type="checkbox" name="day" value="sunday"/>Sunday</div>
                  <div><input type="checkbox" name="day" value="monday"/>Monday</div>
                  <div><input type="checkbox" name="day" value="tuesday"/>Tuesday</div>
                </div>
                <div className="days-container">
                  <div><input type="checkbox" name="day" value="wednesday"/>Wednesday</div>
                  <div><input type="checkbox" name="day" value="thursday"/>Thursday</div>
                  <div><input type="checkbox" name="day" value="friday"/>Friday</div>
                </div>
                <div className="days-container">
                  <div><input  type="checkbox" name="day" value="saturday"/>Saturday</div>
                </div>

                
                <p id="time-from">Time From</p>
                <div className="time-from">
      
                  <input type="time" name="time-from"/>
                  <p>to</p>
                  <input type="time" name="time-to"/>
                </div>
                <br/>
                Location <input className="text-box" type="text" name="location"/>
                <p id="pvt"><input type="checkbox" name="private" value="private"/>Private</p>
                <section id="createAClassSubmit">
                  <input type="submit" form="createAClass" id="createAClassButton"/>
                </section>
              </form>
            </section>
      
      
          </main>
          
          
        )
    }


}

export default CreateAClassModal;