import React from 'react';
import './CreateAQuestionModal.css';

class CreateAQuestionModal extends React.Component {
  constructor(props) {
    super(props);
    this.handleModalClose = this.handleModalClose.bind(this);
  }

  handleModalClose(e) {
    const { onModalClose } = this.props;
    onModalClose(e);
  }

  render() {
    return (
      <div className="modal-wrapper">
        <main id="createAQuestionModal" className="our-modal">
          <header>
            <span className="close" onClick={this.handleModalClose}>&times;</span>
            <h1>Create a Question</h1>
          </header>
          <section className="modal-content">
            <form className="Question" action="index.html" method="post">
              <textarea name="createQuestion" rows="10" cols="30" placeholder="Enter Question here" maxLength="240"></textarea>
            </form>
            <button type="button" className="btn">Submit</button>
            <button className="btn btn-main " onClick={this.handleModalClose}>Cancel</button>
          </section>
        </main>
      </div>
    )
  }
} 

export default CreateAQuestionModal;