import React, { Component } from 'react';
// import './SideBar/SideBar.css';
// import './DashboardClassList.css';

class ClassList extends Component {

  constructor(props) {
    super()
    this.createList = this.createList.bind(this);
    this.clickOnClass = this.clickOnClass.bind(this);
  }

  clickOnClass = (e) =>{
    this.props.onClickOnClass({role: e.target.className, id: e.target.getAttribute('data-id'), title: e.target.textContent});
  }
  createList = () => {
    let teacherList = [];
    let studentList = [];
    let i = 0;
    let j = 0;
    if(this.props.classes.teacher){
      teacherList = this.props.classes.teacher.map((theClass) =>
        <li className="teacher" key={theClass.title} onClick={this.clickOnClass} data-id={theClass.class.id}>{theClass.title}</li>
      );
      i++;
    }
    if(this.props.classes.student){
      j++;
      studentList = this.props.classes.student.map((theClass) => 
      <li className="student" key={theClass.title} onClick={this.clickOnClass} data-id={theClass.class.id}>{theClass.title}</li>
      );
    }
    return teacherList.concat(studentList);
  }

  render() {
    return (
          <ul>{this.createList()}</ul>
          	          
    );
  }
}

export default ClassList;
