import React from 'react';
import Modals from '../../Modals/Modals';
import './Header.css';

import { topics, classes } from '../../../firebase'


export default class Header extends React.Component {
  constructor(props) {
    super();
    this.state = {
      modalVisible: false,
      targetModal: 'Default',
      view: props.view,
      header: props.header,
      user: props.user,
      currentClass: props.currentClass
    }

    this.toggleModals = this.toggleModals.bind(this);
    this.addTopic = this.addTopic.bind(this);

  }

  toggleModals(e) {
    e.preventDefault();
    const { modalVisible, targetModal } = this.state;
    this.setState({
      modalVisible: !modalVisible,
      targetModal: targetModal === 'Default' ? e.target.value : 'Default',
    })
  }

  addTopic(e) {
    e.preventDefault();
    let myObj = {};
    myObj['title'] = e.target[0].value;
    myObj['questions'] = [];
    myObj['votes'] = 0;

    console.log(" current class is ", this.props.currentClassObject);

    let oldTopicList = this.props.currentClassObject.topics;
    const _this = this

    topics.add(myObj).then(function(newTopic){
      let newTopicList = oldTopicList.concat([newTopic])

      _this.props.currentClassObjectRef.update({
        "topics": newTopicList
      }).then(function(res){
        console.log("topic succesfully created!")
      }).catch(function(err){
        console.log("err ", err);
      }).catch(function(err){
        console.log("err ", err);
      })
    })
  }

  render() {
    if(this.props.view === "dashboard"){
      return (
      <div>
        <div className="title">
        <h1>Teach Me More</h1>
      </div>
        <div className="mainTop">
          <div className="header container-fluid">
          <div className="headerButtons">
               <button className="btn btn-main createClass btn-header" id="createClass" type="button" value="CreateAClassModal" onClick={this.toggleModals}>Create a Class</button>
              <button className="btn btn-main findAClass btn-header" id="findClass" type="button" value="FindAClassModal" onClick={this.toggleModals}>Find a Class</button>
          </div>
          <h2>{this.props.header}</h2>
          <Modals toggle={this.state.modalVisible} value={this.state.targetModal} onModalClose={this.toggleModals} user={this.state.user}/>
        </div>
       </div>
       </div>
      )
    } else if(this.props.view === "student")

    {
      return (

        <div>
        <div className="title">
            <h1>{this.props.currentClass}</h1>
          </div>
          <div className="header container-fluid">
            <h2>{this.state.header}</h2>
            <Modals toggle={this.state.modalVisible} value={this.state.targetModal} onModalClose={this.toggleModals}/>
          </div>
        </div>
      )
    } else if(this.props.view === "teacher") {
      return (

        <div>
        <div className="title">
            <h1>{this.props.currentClass}</h1>
          </div>
          <div className="header container-fluid">
            <div className="headerButtons">
              <button type="button" className="btn btn-main btn-header" value="ManageStudentsModal" onClick={this.toggleModals}>Manage Students</button>
            </div>
            <h2>{this.state.header}</h2>
            <div class="addTopicForm">
            <form onSubmit={this.addTopic}>
              <input className ="addTopicInput" type="text" id="addTopic" name="addTopic" placeholder=" Type a new topic here..."></input>
              <button className="btn btn-main addTopicBtn" type="submit" id="topicSubmit" value="New Topic">Add Topic</button>
            </form>
            </div>

          <h2>{this.props.header}</h2>
          <Modals toggle={this.state.modalVisible} value={this.state.targetModal} onModalClose={this.toggleModals} />
          </div>
        </div>
      )
    }else {
      return (
        <div>
          not a valid view
        </div>
      )
    }

  }
  
}
