import React, { Component } from 'react';
import './AccountManagementModal.css';

class AccountManagementModal extends Component {
    constructor(props) {
        super(props);
        this.handleModalClose = this.handleModalClose.bind(this);
    }
    
    handleModalClose(e) {
        const { onModalClose } = this.props;
        console.log(onModalClose)
        onModalClose(e)
    }

    render() {
        return (
            <main className="AccountManagementModal our-modal flex-container">
                <span className="close" onClick={this.handleModalClose}>&times;</span>
                <header>
                    <h1>Profile</h1>
                </header>

                <form>
                    <label htmlFor="name">Name:</label>
                    <input type="text" name="name"  required />
                    <label htmlFor="email">Email:</label>
                    <input type="email" name="email" required></input>
                    <label htmlFor="password">Password:</label>
                    <input type="password" name="password" required></input>
                    <label htmlFor="new_password">new Password:</label>
                    <input type="password" name="new_password" required></input>
                    <label htmlFor="confirm_new_password">Confirm new Password:</label>
                    <input type="password" name="confirm_new_password" required></input>


                    <input type="submit" value="Submit"></input>
                    <button className="close"><label onClick={this.handleModalClose}>Cancel</label></button>
                </form>
            </main>
        )
    }
}

export default AccountManagementModal;