import React, { Component } from 'react';
import Modals from '../../Modals/Modals';
import { users, auth } from '../../../firebase';
import './NavBar.css';

export default class NavBar extends Component {
    constructor() {
        super();
        this.state = {
          modalVisible: false,
          targetModal: 'Default',
        }
        this.toggleModals = this.toggleModals.bind(this);
        this.logout = this.logout.bind(this);
      }
    
      toggleModals(e) {
        const { modalVisible, targetModal } = this.state;
        this.setState({
          modalVisible: !modalVisible,
          targetModal: targetModal === 'Default' ? e.target.getAttribute("value") : 'Default',
        })
      }
    
      logout() {
        const { onLogout } = this.props;
        auth.signOut()
        onLogout(null);
      }
      
    render() {
        const { name } =this.props;
        return (
            <div>
                <nav className="navbar navbar-expand-sm navbar-light bg-light">
                    <label className="navbar-brand">
                        <img src={`${process.env.PUBLIC_URL}/logo-icon.png`} alt="navbar apple"></img>
                    </label>
                    <button className="navbar-toggler" type={"button"} data-toggle={"collapse"} data-target={"#navbarText"} aria-controls={"navbarText"} aria-expanded={"false"} aria-label={"Toggle navigation"}>
                        <span className={"navbar-toggler-icon"}></span>
                    </button>
                    <div className="collapse navbar-collapse" id={"navbarText"}>
                        <ul className="navbar-nav mr-auto">
                            <li className="st-view nav-item">
                                <label className="nav-link">Dashboard</label>
                            </li>
                            <li className="nav-item">
                                {/* THE HREF FOR THIS ANCHOR TAG STILL NEEDS TO BE IMPLEMENTED */}
                                <label className="nav-link" onClick={this.logout}>Logout</label>
                            </li>
                            <li className="nav-item">
                            <label className="nav-link" id={"accountManagement"} value="AccountManagementModal" onClick={this.toggleModals}>Profile</label>
                            </li>
                            <li className="nav-item" id={"openClassList"}>
                                <label htmlFor={"slideSidebar"} className="nav-link">Open Class List</label>
                            </li>
                        </ul>
                        <span className="navbar-text">{name}</span>
                    </div>
                </nav>
                <Modals toggle={this.state.modalVisible} value={this.state.targetModal} onModalClose={this.toggleModals} />
            </div>

        )
    }
}
