import React, { Component } from 'react';
import './App.css';
import Login from './Components/Login/Login';
import Main from './Components/Main/Main';

import { users, auth, provider, classes } from './firebase';

// require("firebase/firestore");

class App extends Component {
  constructor() {
    super();
    this.state ={
      user: null,
      loading: true
    }
    this.toggleLogin = this.toggleLogin.bind(this);
  }

  toggleLogin(e) {
    this.setState({
      user: e ? users.doc(e.uid) : null,
    });
  }
  
  componentDidMount() {
    users.get().then(function(querySnapshot) {
      const currentusers = [];
      querySnapshot.forEach(doc => currentusers.push(doc.id));
      return currentusers;
    })
    .then(currentusers => {      
      auth.onAuthStateChanged((user) => {
        const hasAccount = user && currentusers.includes(user.uid);
        if (hasAccount) {
          this.setState({ user: users.doc(user.uid) });
        } 
      });
    })
  
      demoAsyncCall().then(() => this.setState({ loading: false }));
    
  }

  render() {
      const { user } = this.state;
      const { loading } = this.state;
    
      if(loading) { // if your component doesn't have to wait for an async action, remove this block 
        return null; // render null when app is not ready
      }

    return (
      <div className="App">
        {user ?
          <Main user={user} onLogout={this.toggleLogin} />
          :
          <Login onLogin={this.toggleLogin} />
          
        }
      </div>
    );
  }
}
function demoAsyncCall() {
  return new Promise((resolve) => setTimeout(() => resolve(), 2000));
}
export default App;
