import React, { Component } from 'react';
import './Main.css';
import './SideBar/SideBar.css';
import './SearchBar.css';
import ClassList from './ClassList';
import Header from './Header/Header';
import Navbar from './NavBar/NavBar';
import ListPane from './ListPane/ListPane';

import { classes, users } from '../../firebase';
import { listenerCount } from 'events';


class Main extends Component {

  constructor(props) {
    super()
    this.state = {
      view: "dashboard",
      header: "Classes",
      name: "",
      user: props.user,
      classes: [],
      currentClass: "",
      currentClassObject: null,
      currentClassObjectRef: null,
      topics: []
    }
    this.logout = this.logout.bind(this);
    this.clickOnClass = this.clickOnClass.bind(this);
  }



  async clickOnClass(e) {
    const _this = this;
    let classObject = await classes.doc(e.id).get();
    let classRef = classes.doc(e.id);
    let promises = [];
    for(let i = 0; i < classObject.data().topics.length; i++){
      promises.push(classObject.data().topics[i].get());
    }
    Promise.all(promises).then(function(values){
      let newTopics = [];
      for(let i = 0; i < values.length; i++){
        newTopics.push(values[i].data());
      }
      _this.setState({
        view: e.role,
        header: "Topics",
        currentClass: e.title,
        currentClassObject: classObject.data(),
        currentClassObjectRef: classRef,
        topics: newTopics
      })
    })
  }

  logout(e) {
    const { onLogout } = this.props;
    onLogout(e);
  }

  componentDidMount() {
    const { user } = this.props;
    user.get()
    .then(user => user.data())
    .then(user => {
      this.setState({
        name: user.name,
        classes: user.classes
      }, function(){
      })
    })
    // 
  }

  render() {
    const { name } = this.state;
    
    return (
      <div className="Main">
        <h1>I'm the main page</h1>
        <input id="slideSidebar" type="checkbox" role="button" />
        <label htmlFor="slideSidebar" id="sideBarLabel"><span>Open Class List</span></label>
        <section className="sidebar">
          <label htmlFor="slideSidebar" className="closeSideBar">&times;</label>
          <h2>My Classes</h2>
            <ClassList classes={this.state.classes} onClickOnClass={this.clickOnClass}/>
        </section>
        <div className="main-wrap">
          <Navbar name={name} onLogout={this.logout} />
          <Header view={this.state.view} header={this.state.header} user={this.state.user} currentClass={this.state.currentClass} currentClassObject={this.state.currentClassObject} currentClassObjectRef={this.state.currentClassObjectRef}/>
          <div className="main-search">
            <form id="mainSearch">
              <input type="text" id="search" name="search" placeholder="Start typing to search"/>
            </form>
          </div>
          <ListPane view={this.state.view} classes={this.state.classes} onClickOnClass={this.clickOnClass} currentClassObject={this.state.currentClassObject} topics={this.state.topics}/>
        </div>
      </div>
    );
  }
}

export default Main;
